        <div class="sub-container">
          <h1 class="name">{{ project.name }}</h1>
          <img class="image" :src="getImgUrl(project.imageSrc)" alt="failed to load" />
        </div>
        <a :href="project.githubUrl" target="_blank"><fa-icon :icon="['fab', 'github-square']" size="4x" class="github" /></a>
        <p class="brief" v-html="project.brief"></p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      projects: [
        {
          name: "QuizAtshi",
          imageSrc: "QuizAtshi.png",
          githubUrl: "https://github.com/MohamedMetwalli5/QuizAtshi",
          brief: `• User can switch to Dark Mode.<br/>
                  • User can choose the number of questions and their difficulty level.<br/>
                  • User is asked about the countries flags.<br/>
                  • For every right answer, the user is given a point and is shown the correct answer.<br/>
                  • User grade is shown after answering all questions.`,
        },
        {
          name: "Zarka",
          imageSrc: "Zarka.png",
          githubUrl: "https://github.com/MohamedMetwalli5/Zarka",
          brief: `• Implementation of a Key-Value store that is inspired by Cassandra's architecture to be named "Zarka".<br/>
                  • LSM-Tree needs is used as our storage data structure.<br/>
                  • Consistent Hashing is used as our partition rebalancing strategy (Only adding a new node should be supported).<br/>
                  • Leaderless replication is used with configurable quorum sizes.`,
        },
        {
          name: "Paint",
          imageSrc: "WebBasedDrawingWebsite.png",
          githubUrl: "https://github.com/MohamedMetwalli5/Paint",
          brief: `• User can draw multiple shapes with different colors.<br/>
                  • User can change the shape color, position, and size.<br/>
                  • User can replicate shapes using the ”copy” operation.<br/>
                  • User can use the ”undo” or the ”redo” operation to undo or redo any action.`,
        },
        {
          name: "Bookstore",
          imageSrc: "Bookstore.png",
          githubUrl: "https://github.com/MohamedMetwalli5/Bookstore",
          brief: `• User can edit his personal information including his password.<br/>
                  • User can search for books by any of the book’s attributes.<br/>
                  • User can add books to a shopping cart & checkout a shopping cart.<br/>
                  • User can View the items in the cart, total prices of the books in the cart, and Removing items from the cart.`,
        },
        {
          name: "Music Blender",
          imageSrc: "MusicBlender.png",
          githubUrl: "https://github.com/MohamedMetwalli5/Music-Blender",
          brief: `• It is a game in which each player can mix more than 60 different tones to produce new music.<br/>
                  • Used visual effects to show the tunes.`,
        },
        {
          name: "Physics Collisions",
          imageSrc: "PhysicsCollision.png",
          githubUrl: "https://github.com/MohamedMetwalli5/Physics-Collisions",
