          law. It has a detection system to detect collisions between objects.`,
        },
        {
          name: "Dots and Boxes",
          imageSrc: "DotsAndBoxes.png",
          githubUrl: "https://github.com/MohamedMetwalli5/Dots-and-Boxes-game",
