          Usually two players take turns adding a single horizontal or vertical yline between two un-joined adjacent dots.<br />
          A player who completes the fourth side of a 1×1 box earns one point and takes another turn.<br/> 
          The game ends when no more lines can be placed.<br/>
          The winner is the player with the most points.`,
        }
      ]
      }
