  float: left;
  color: #000;
  font-size: 3em;
  font-family: 'Open Sans', sans-serif;
}

/* Set styles for the GitHub icon */
.github {
  margin-top: 0.3em;
  margin-left: 20px;
  margin-right: 40px;
  color: #fff;
}

